
/*Desktop View*/
.navbar {
    display: flex;
    background: black;
    justify-content: space-between;
    align-items: center;
    padding: 0px 40px;
    height: 80px;
  }
  h2{
      color: white;
  }
  .logo-nav {
    display: flex;
    align-items: right;
    justify-content: space-between;
  }
  .logo-container {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .logo {
    width: 45px;
    height: 45px;
  }
  .nav-options {
    /* padding-left: 25px;
    display: grid;
    grid-template-columns: repeat(3, auto);
    grid-gap: 50px;
    list-style-type: none; */
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin-right:40px;
    margin-left: 920px;
  }
  .mobile-option {
    display: none;
  }
  .option{
      display: flex;
      justify-content: space-between;
      color: grey;
  }
  .option a{
      text-decoration: none;
      padding: 9px;
      color: rgb(201, 199, 199);
      box-shadow: 1px 2px 2px 2px grey;
      margin: 10px;
      justify-content: space-between;
    
  }
  .option :hover {
    color: white;
  }
  .signin-up {
    display: flex;
    padding: 0px 5px;
    list-style-type: none;
  }
  .sign-in {
    padding-right: 50px;
    align-self: center;
  }
  .sign-in :hover {
    color: white;
  }
  .signup-btn {
    padding: 10px 10px;
    height: 2.4rem;
    border-radius: 3px;
    background: rgb(222, 9, 241);
    color: white;
    cursor: pointer;
    align-items: center;
  }
  .mobile-menu {
    display: none;
  }
  @media (max-width: 648px) {
    /*Mobile View */
    .navbar {
      padding: 0px 10px;
    }
    .logo {
      width: 45px;
      height: 45px;
    }
    .nav-options {
      display: flex;
      width: 100%;
      height: 350px;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 0;
      transition: all 0.5s ease;
      flex-direction: column;
      list-style-type: none;
      grid-gap: 0px;
    }
    .nav-options.active {
      background: rgba(0, 0, 0, 0.5);
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
      align-content: center;
      padding-left: 0px;
    }
  
    .menu-icon {
      width: 45px;
      height: 45px;
    }
    .option {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 10vw;
      padding: 30px 0px;
    }
    .sign-up {
      background: rgb(0, 0, 0, 0.5);
      border-radius: 3px;
      color: white;
      padding: 20px 0px;
      width: 80%;
      align-self: center;
      border-radius: 3px;
    }
  
    .signin-up {
      display: none;
    }
    a{
        text-decoration: none;
        color: white;
    }
  }
  .mobile-menu {
    display: block;
  }
  