.footer{
    /* background-color: rgba(0, 0, 0, 0.8); */
    background-color: #1F2833;
    color: white;
    
}
.ul{
    text-decoration: none;
    display: flex;
    flex-direction: row;
    padding: 20px;
    margin: 30px;    
    margin-bottom: 0;
    padding-bottom: 0;
}
.a-tag{
    padding: 15px;
    color: rgb(211, 209, 209);
}
.copyright{
    padding-left: 45px;
    margin: 20px;
    color: rgb(172, 168, 168);
}
.footer-links{
    display: flex;
    flex-direction: row;
    /* margin: 10px; */
    padding-left: 45px;
    justify-content: space-between;
}
.icons a{
    padding: 10px;
    margin: 10px;
    text-decoration: none;
}
.icons a i{
    height: 30px;
    padding: 10px;
}
.image{
    height: 62px;
    margin-bottom: 10px;
    padding: 7px;
    padding-bottom: 10px;
}

@media screen and (max-width:754px) {
    .ul{
        text-decoration: none;
        display: flex;
        flex-direction: column;
        padding: 12px;
        margin: 20px;    
        margin-bottom: 0;
        padding-bottom: 0;
        font-size: 20px;
        text-align: center;
    }
    .ul a{
        text-decoration: none; 
        padding: 6px;
        margin: 6px;
    }
    .footer{
        background-color: #1F2833;
        color: white;
        margin-bottom: 0px;
        padding-bottom: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
}